@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Connectbar {
  background-color: #000000;

  color: white;
  display: flex;

  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.Connect {
  font-size: 25px;
  font-family: 'Montserrat', sans-serif;
  font-weight: bold;
  flex-grow: 2;
  padding-left: 10vw;
}
.Iconsgroup {
  display: flex;

  flex-grow: 8;
  flex-direction: row;
}

.IconBackground {
  width: 60px;
  height: 60px;
  text-align: center;
  line-height: 60px;
  font-size: 35px;
  margin: 0 10px;
  border-radius: 50%;
  position: relative;
  overflow: hidden;
  border: 3px solid #fff;
  z-index: 1;
  color: #fff;
}

.Icon {
  position: relative;
  color: #fff;
  transition: 0.5s;
  z-index: 3;
}

.Sidebar {
  background-color: #202020;
  min-width: 25vw;
  min-height: 73.1vh;
  color: white;
  font-size: 20px;
  padding-top: 5rem;
  padding-left: 3rem;
  /*color: 'rgba(255,255,255,.5)'*/
}
.Head {
  font-family: 'Montserrat', sans-serif;
  font-size: 55px;
  font-weight: bold;
}
.Subhead {
  font-family: Arial;
  font-size: 25px;
  font-weight: lighter;
  opacity: 0.7;
  margin-bottom: 1rem;
  /*color: '#515151'*/
}
.Hoverbuttons {
  line-height: 1.75rem;
}
.Hoverbuttons:hover {
  padding-top: 5px;
}

.Mainbar {
  display: flex;
  background-color: #111;
  color: white;
  min-width: 70vw;
  align-items: center;
  font-size: 14px;
  flex-direction: column;
  padding-top: 25vh;
}
.Break {
  display: flex;
  flex-basis: 10%;
  width: 100%;
  height: 0;
}

.Bars {
  display: flex;
  flex-direction: row;
}

